@import "sass/helpers/variables";

@font-face {
  font-family: "Averta";
  src: url("../fonts/Averta-Regular/Averta-Regular.woff") format("woff"),
  url("../fonts/Averta-Regular/Averta-Regular.eot") format("opentype"),
    url("../fonts/Averta-Regular/Averta-Regular.ttf") format("truetype"),
    url("../fonts/Averta-Regular/Averta-Regular.svg#Averta-Regular")
      format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Averta";
  src: url("../fonts/Averta-Semibold/Averta-Semibold.woff") format("woff"),
  url("../fonts/Averta-Semibold/Averta-Semibold.eot") format("opentype"),
  url("../fonts/Averta-Semibold/Averta-Semibold.ttf") format("truetype"),
  url("../fonts/Averta-Semibold/Averta-Semibold.svg#Averta-Semibold")
    format("svg");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Averta";
  src: url("../fonts/Averta-Bold/Averta-Bold.woff") format("woff"),
  url("../fonts/Averta-Bold/Averta-Bold.eot") format("opentype"),
    url("../fonts/Averta-Bold/Averta-Bold.ttf") format("truetype"),
    url("../fonts/Averta-Bold/Averta-Bold.svg#Averta-Bold") format("svg");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

input,
textarea,
select {
  color: #5b5863 !important;
  box-shadow: none !important;
  outline: none !important;
  height: auto;

  &.form-control {
    height: auto;
  }

  ::placeholder {
    color: #8d8b92;
  }

  &:focus {
    &::-webkit-input-placeholder {
      opacity: 0.5;
      transition: opacity 0.3s ease-in-out;
      box-shadow: none;
      outline: none;
    }
  }
}

.referral-bar {
  background-color: $primaryColor;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  padding: 8px 0;
  position: relative;
  z-index: 2;
  display: none;
  p {
    font-weight: 500;
    font-size: 13px;
    line-height: 16px;
    color: #fafafa;
    margin: 0 10px;
  }
}

#zdbutton {
  padding: 0.6rem 1.57143rem;
  border-radius: 999rem;
  background-color: $primaryColor;
  color: #ffffff;
  position: fixed;
  bottom: 15px;
  right: 15px;
  letter-spacing: 0.6px;
  font-size: 1rem;
  border: none;
  font-weight: 700;
  z-index: 9999;
  box-shadow: inset 0 0 0 0.21429rem rgba(255, 255, 255, 0.4);
  @media #{$mobile} {
    padding: 7px 20px;
  }
}

.fw-500 {
  font-weight: 500;
}

.h-70 {
  height: 70px !important;
}

.new-green-tag {
  font-size: 11px;
  line-height: 12px;
  padding: 3px 5px;
  display: inline-block;
  text-transform: uppercase;
  background: linear-gradient(88.46deg, #08AE7E 33.06%, #0BDFA2 104.26%);
  border-radius: 9px 9px 9px 0px;
  color: #fff;
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  -webkit-appearance: none;
}

.wevpn-link {
  color: $primaryColor;
  text-decoration: underline !important;
  transition: all 0.3s ease-in-out;
  font-weight: 500;

  i,
  svg {
    font-weight: bold;
    margin-left: 5px;
    transition: all 0.3s ease-in-out;
  }

  &:hover {
    color: $linkHoverColor !important;
    text-decoration: none !important;

    i,
    svg {
      transform: translateX(3px);
    }
  }
}

.mainColor {
  color: $primaryColor;
}

.copy-ip {
  background: #fd3e72;
  border-radius: 5px;
  cursor: pointer;
  position: relative;
  width: 40px;
  height: 40px;
  text-align: center;
  line-height: 40px;
  display: inline-block;
  &:hover {
    .tooltiptext {
      visibility: visible;
      opacity: 1;
    }
  }
  .tooltiptext {
    visibility: hidden;
    background-color: #4f4f4f;
    font-size: 12px;
    color: #fff;
    text-align: center;
    border-radius: 5px;
    padding: 3px 11px;
    position: absolute;
    z-index: 1;
    bottom: 115%;
    left: 50%;
    transform: translatex(-50%);
    opacity: 0;
    transition: opacity 0.3s;
    font-weight: 500;
    line-height: 24px;
    @media #{$mobile} {
      font-size: 12px;
      padding: 4px 8px;
    }

    &.changed {
      background-color: #08ae7e;
      &:after {
        border-color: #08ae7e transparent transparent transparent;
      }
    }
    &:after {
      content: "";
      position: absolute;
      top: 100%;
      left: 50%;
      margin-left: -5px;
      border-width: 5px;
      border-style: solid;
      border-color: #4f4f4f transparent transparent transparent;
    }
  }
}

.main-bg {
  background-color: #f9f8f4;
}

select {
  background: url(../imgs/Arrow.png) no-repeat right;
  -webkit-appearance: none;

  &:invalid {
    color: #8d8b92;
  }

  & option:first-child {
    color: #8d8b92;
  }

  &:invalid option:not(:first-child) {
    color: #5b5863;
  }
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  /* display: none; <- Crashes Chrome on hover */
  -webkit-appearance: none;
  margin: 0;
  /* <-- Apparently some margin are still there even though it's hidden */
}

.bull {
  color: #c1c0c4;
}

input[type="number"] {
  -moz-appearance: textfield;
  /* Firefox */
}

body {
  font-family: "Averta", "Segoe UI", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-style: normal;
  position: relative;
  overflow-x: hidden;
  color: #5b5863;
  background-color: #f9f8f4;

  &.body-dashboard {
    background-color: #f2f2ed;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-bottom: 0;
  font-weight: bold;
}

p {
  margin-bottom: 0;
}

::selection {
  background-color: #5b5863;
  color: #fff;
}

a {
  color: #5b5863;
  text-decoration: none !important;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

@media (min-width: 1200px) {
  .container {
    max-width: 1200px;
    position: relative;
  }
}

@media #{$mobile} {
  .container {
    padding: 0 20px;
    position: relative;
  }
}

.wrapper {
  overflow: hidden;
}

/* Change the white to any color */
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 30px white inset !important;
}

/* Change text in autofill textbox */
input:-webkit-autofill {
  -webkit-text-fill-color: #5b5863 !important;
}

.heading-medium {
  font-size: 36px;
  line-height: 44px;

  @media #{$tablet} {
    font-size: 30px;
    line-height: 38px;
  }
  @media #{$mobile} {
    font-size: 24px;
    line-height: 28px;
  }
}

button:focus {
  box-shadow: none;
  outline: none;
}

body.overlay {
  overflow: hidden;
  transition: all 0.3s ease-in-out;
}

body.overlay:after {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(#000, 0.5);
  z-index: 1;
  margin: 0;
  overflow: hidden;
}

.arrow {
  position: absolute;
  top: 16px;
  right: 16px;
  width: 13px;
  height: 21px;
  display: inline-block;

  span {
    top: 10px;
    position: absolute;
    width: 8px;
    height: 2px;
    background-color: #acaaae;
    display: inline-block;
    transition: all 0.4s ease;

    &:first-of-type {
      left: 0;
      transform: rotate(45deg);
    }

    &:last-of-type {
      right: 0;
      transform: rotate(-45deg);
    }
  }
}
// Typography
h1 {
  font-size: 47px;
  line-height: 63px;
}
h2 {
  font-size: 38px;
  line-height: 44px;
  font-weight: 500;
}
h3 {
  font-size: 24px;
  line-height: 32px;
  font-weight: 500;
}
h4 {
  font-size: 20px;
  line-height: 28px;
  font-weight: 500;
}
h5 {
  font-size: 16px;
  line-height: 22px;
  font-weight: 500;
}
h6 {
  font-size: 14px;
  line-height: 20px;
  font-weight: 500;
}
.p-lg {
  font-size: 18px;
  line-height: 24px;
}
.p-md {
  font-size: 16px;
  line-height: 22px;
}
.p-sm {
  font-size: 14px;
  line-height: 20px;
}
.p-xs {
  font-size: 12px;
  line-height: 18px;
}

// header
/* Start Header */
.top-header {
  position: relative;
  z-index: 3;
  padding: 8px 0;
  background-color: #fff;
  border-bottom: 1px solid #ebeae6;
  height: 33px;
  &.dark {
    border: 0;
    background-color: rgba(26, 40, 48, 0.95);
    .top-header-content ul li {
      color: #9c99a2;
    }
    .top-header-content ul li span {
      &#location,&#ip,&#isp {
        color: #fff;
      }
    }
    
  }
  
  &.fixed-top-header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
  }

  .top-header-content {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #a29cb0;
    position: relative;
    z-index: 1;
    @media #{$mobile} {
      flex-direction: column;
    }
    ul {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      margin-right: 22px;
      @media #{$mobile} {
        margin-right: 0;
      }

      li {
        font-size: 13px;
        line-height: 16px;
        color: #a29cb0;
        span:first-child {
          color: #A29CB0;
          text-decoration: none !important;
        }
        @media #{$mobile} {
          font-size: 12px;
          a {
            font-size: 12px;
          }
        }
        .bull {
          color: #a29cb0;
          margin: 0 10px;
          @media #{$mobile} {
            margin: 0 4px;
          }
        }
        .exposed {
          font-weight: 500;
        }
      }
    }
    ul li a span {
      text-decoration: underline !important;
      color: $primaryColor !important;
    }
    a {
      text-decoration-line: underline !important;
      color: $primaryColor;
      font-size: 13px;
      line-height: 16px;
      font-weight: 500;
      
      &:hover {
        text-decoration: none !important;
        i {
          transform: translateX(6px);
        }
        svg {
          transform: translateX(6px);
        }
      }
      svg {
        transform: translateX(4px);
        transition: 0.3s;
      }
      i {
        transform: translateX(4px);
        transition: 0.3s;
      }
    }
  }
}

header {
  position: relative;
  .logo-white {
    display: none;
  }
  &.get-started-page {
    .container {
      border-bottom: 0 !important;
      @media #{$maxTab} {
        padding-bottom: 0 !important;
      }
    }
  }

  // &.border--bottom {
  //   .container {
  //     @media (min-width: 1024px) {
  //       position: relative;
  //       &:after {
  //         content: "";
  //         height: 1px;
  //         position: absolute;
  //         background-color: #ebe8df;
  //         bottom: -21px;
  //         left: 15px;
  //         right: 15px;
  //       }
  //     }
  //   }
  // }

  &.homepage {
    #nav-icon span {
      background-color: #fff;
    }
    #nav-icon.open span:nth-child(1),
    #nav-icon.open span:nth-child(3) {
      background-color: $primaryColor;
    }

    &.dashboard-page p {
      a:first-child {
        color: #fff;
        transition: all 0.3s ease-in-out;
        &:hover {
          color: $primaryColor;
        }
      }
    }
  }

  &.header-white,&.homepage {
    .navbar-brand {
      .logo-colored {
        display: none;
      }
      .logo-white {
        display: inline-block;
      }
    }
  }

  &.header-white {
    .navbar-light .navbar-nav .nav-link {
      color: #fff;
      transition: all 0.3s ease-in-out;
      @media #{$maxTab} {
        color: #5b5863;
      }
    }
    .dropdown-toggle::after {
      background-image: url(../imgs/arrow-w.png);
      @media #{$maxTab} {
        background-image: url(../imgs/arrow.svg);
      }
    }
    .navbar-light .navbar-nav .nav-link:focus,
    .navbar-light .navbar-nav .nav-link:hover {
      color: #fff !important;
      opacity: 0.8;

      @media #{$maxTab} {
        color: #5b5863 !important;
        opacity: 1;
      }
    }
  }
  &.windows-header {
    #nav-icon {
      background-color: #fff;
    }
  }
  .navbar-show-mobile {
    background-color: #fff !important;
    position: fixed !important;
    &.white_logo {
      .navbar-brand{
        img.logo-colored {
          display: inline-block !important;
        }
        img.logo-white {
          display: none !important;
        }
      }
    }
  }

  .navbar {
    padding: 13px 0;
    position: absolute;
    width: 100%;
    z-index: 5;
    border-bottom: 1px solid transparent;
    @media (max-width: 991.98px) {
      &.navbar-expand-lg .container {
        padding-right: 0;
        padding-left: 0;
      }
    }
    > div {
      width: 100%;
    }
    .container {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: space-between;
      width: 100%;
    }

    &.nav-fixed {
      position: fixed;
      top: 0;
      left: 0;
      background-color: #fff;
      border-bottom: 1px solid #ebe8df;
      animation: q 1s cubic-bezier(0.23, 1, 0.32, 1) both;

      @keyframes q {
        0% {
          transform: translateY(-100%);
        }
        100% {
          transform: translateY(0);
        }
      }
      .login .login-item .login-link,
      .navbar-nav .nav-link {
        color: #5b5863 !important;
        &:hover {
          color: $primaryColor !important;
        }
      }

      .dropdown-toggle::after {
        background-image: url(../imgs/arrow.svg);
      }

      .login .login-item .get-started {
        background-color: $primaryColor !important;
        color: #fff !important;
        &:hover {
          background-color: $linkHoverColor !important;
        }
      }

      .navbar-brand img.logo-white {
        display: none !important;
      }
      .navbar-brand img.logo-colored {
        display: block !important;
      }

      .get-started-login.color-bg-pages p a:first-child {
        color: #5b5863;
        @media #{$maxTab} {
          padding: 12px 14px 12px 0;
          display: inline-block;
        }
      }

      #nav-icon span {
        background-color: $primaryColor;
      }

      .navbar-nav .active > .nav-link {
        color: $primaryColor !important;
      }

      .container:after {
        display: none;
      }
    }

    .nav-item {
      &.dropdown {
        > a:hover {
          cursor: text;
        }
        &.last > a:hover {
          cursor: pointer;
        }
      }
      li {
        .badge {
          text-transform: uppercase;
          font-size: 10px;
          font-weight: 400;
          padding: 4px 6px;
          border-radius: 3px;
          line-height: normal;
          &-secondary {
            background-color: #b3b3b3;
          }
          &-danger {
            background-color: $primaryColor;
          }
        }
      }
      @media (min-width: 992px) {
        &.dropdown {
          position: relative;

          &:hover {
            .dropdown--menu {
              opacity: 1;
              visibility: visible;
              transform: translateY(0);
            }

            .dropdown-toggle {
              &::after {
                transform: rotate(180deg);
              }
            }
          }

          .dropdown--menu {
            position: absolute;
            background-color: #fff;
            border-radius: 5px;
            opacity: 0;
            visibility: hidden;
            transition: opacity, transform 1s cubic-bezier(0.4, 0, 0.2, 1);
            border: 1px solid #e2e0d4;
            box-shadow: 0px 10px 16px rgba(135, 134, 131, 0.28);
            transform: translateY(10px);
            z-index: 9999;
            display: flex;
            left: -158px;
            width: 1170px;
            &:after,
            &:before {
              border-left: 11px solid transparent;
              border-right: 11px solid transparent;
              border-bottom: 11px solid #fff;
              top: -11px;
              content: "";
              left: 176px;
              position: absolute;
            }

            /* Styling for second triangle (border) */

            &:before {
              border-left: 12px solid transparent;
              border-right: 12px solid transparent;
              border-bottom: 12px solid #e2e0d4;
              border-bottom-color: inherit;
              top: -12px;
              left: 175px;
            }

            ul {
              flex: 1;
              padding: 32px;
              position: relative;
              &:last-child {
                flex: 1.15;
                @media #{$betweentabdesk} {
                  flex: 1;
                }
              }

              &:not(:last-child) {
                &:after {
                  content: "";
                  position: absolute;
                  height: 100%;
                  top: 0;
                  right: 0;
                  width: 1px;
                  background-color: #ebe8df;
                }
              }

              li {
                margin-bottom: 14px;

                &.active {
                  a {
                    color: $primaryColor !important;

                    svg {
                      path {
                        fill: $primaryColor;

                        &.line {
                          stroke: $primaryColor;
                          fill: none;
                        }
                      }
                    }
                  }
                }

                &:last-child {
                  margin-bottom: 0;
                }

                &.disabled {
                  a:hover {
                    color: #5a5761;
                    cursor: text;
                    svg {
                      path {
                        fill: #5a5761;
                        &.line {
                          stroke: #5a5761;
                        }
                      }
                    }
                  }
                }

                a {
                  display: inline-block;
                  padding: 5px 0;
                  transition: color 0.2s ease-in;
                  color: #5a5761;
                  font-family: "Averta";
                  font-weight: 500;

                  &:hover {
                    color: $primaryColor;

                    svg {
                      path {
                        fill: $primaryColor;

                        &.line {
                          stroke: #fd3e72;
                          fill: none;
                        }
                      }
                    }
                  }

                  svg {
                    position: relative;
                    top: -3px;
                    margin-right: 5px;

                    path {
                      transition: all 0.2s ease-in;
                    }
                  }
                }

                &:first-child,
                &.browser {
                  font-size: 14px;
                  color: #8d8b92;
                  padding-top: 0;
                }
              }
            }

            &.second,
            &.third {
              width: 280px;
              left: -25px;

              &:before,
              &:after {
                left: 55px;
              }

              &:before {
                left: 54px;
              }

              ul {
                float: none;
                width: 100%;
                padding-right: 0;

                li {
                  &:not(:last-child) {
                    margin-bottom: 5px;
                  }

                  &:first-child {
                    a {
                      color: #5a5761;
                      font-size: 16px;

                      &:hover {
                        color: $primaryColor;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }

      @media #{$maxTab} {
        &.dropdown {
          &.active {
            .dropdown--menu {
              display: block !important;
            }

            .dropdown-toggle {
              &:after {
                transform: rotate(180deg);
              }
            }
          }

          .dropdown--menu {
            padding: 0 0 0 20px;
            position: relative;
            top: -10px;
            display: none;
            justify-content: space-between;
            transition: all 0.3s ease-in-out;
            border-left: 1px solid #dfddd6;
            margin-top: 20px;

            ul {
              text-align: left;
              padding-right: 0;
              position: relative;
              flex-basis: 30%;
              &:last-child {
                li:last-child {
                  margin-bottom: 0;
                  a {
                    margin-bottom: -12px;
                  }
                }
              }

              &:nth-child(2) {
                margin-right: 4%;
              }

              li {
                line-height: 20px;
                margin-bottom: 8px;
                &.active {
                  a {
                    color: $primaryColor !important;

                    svg {
                      path {
                        fill: $primaryColor;

                        &.line {
                          stroke: $primaryColor;
                          fill: none;
                        }
                      }
                    }
                  }
                }

                &:last-child {
                  margin-bottom: 20px;
                }

                a {
                  font-size: 16px;
                  padding: 12px 10px;
                  font-weight: 500;
                  display: inline-block;
                  transition: color 0.2s ease-in;
                  color: #5a5761;

                  &:hover {
                    color: $primaryColor;

                    svg {
                      path {
                        fill: $primaryColor;

                        &.line {
                          stroke: #fd3e72;
                          fill: none;
                        }
                      }
                    }
                  }

                  svg {
                    position: relative;
                    margin-right: 5px;
                    top: -2px;
                    path {
                      transition: all 0.2s ease-in;
                    }
                  }
                }

                &:first-child,
                &.browser {
                  font-size: 16px;
                  color: #8d8b92;
                  padding: 0 10px 15px;
                  margin-bottom: 0;
                }
              }
            }

            &.second,
            &.third {
              display: none;
              ul {
                li {
                  padding: 0;

                  a {
                    margin-left: 0;
                    font-weight: 500;
                  }

                  &:first-child {
                    a {
                      margin-top: -12px;

                      &:hover {
                        color: $primaryColor;
                      }
                    }
                  }
                  &:last-child {
                    margin-bottom: 0;
                    a {
                      margin-bottom: -12px;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }

    @media #{$maxTab} {
      padding: 13px 20px;

      .navbar-collapse {
        border-top: 1px solid #ebe8df;
        margin: 20px 0 0;
        max-height: 530px;
        overflow: auto;
      }
    }

    .navbar-brand {
      margin-right: 33px;
      padding: 0 0 3px 0px;
      img {
        height: 40px;
        width: 124px;
      }
      @media #{$maxTab} {
        margin: 0;
        padding: 0;

        img {
          height: 35px;
        }
      }
    }

    .navbar-nav {
      @media #{$maxTab} {
        padding-top: 15px;

        .nav-item {
          font-size: 14px;
          line-height: 40px;
          margin-bottom: 8px;
        }
      }
    }

    .login {
      .login-item {
        display: inline-block;

        &:first-child {
          margin-right: 12px;
        }

        &:last-child {
          @media #{$maxTab} {
            display: block;
            border-top: 1px solid #ebe8df;
            margin-top: 20px;
            padding-top: 25px;
          }
        }

        .login-link {
          font-size: 16px;
          line-height: 19px;
          color: inherit;
          padding: 12px 14px 12px 0;
          display: block;
          transition: color 0.3s ease-in-out;
          font-family: "Averta";
          font-weight: 500;
          text-decoration: none;

          @media #{$maxTab} {
            color: #5b5863;
            font-size: 18px;
          }

          &:hover {
            color: $primaryColor;
          }
        }

        .get-started {
          padding: 10px 20px;
          display: block;
          font-size: 14px;
          text-align: center;
          transition: background-color 0.3s ease-in-out;
          background-color: $primaryColor;
          text-decoration: none;
          color: #fff;
          font-family: "Averta";
          font-weight: 500;
          border-radius: 3px;

          @media #{$maxTab} {
            padding: 13.5px;
            color: #fff;
            background-color: $primaryColor;
          }

          &:hover {
            background-color: $linkHoverColor;
          }
        }
      }
    }
  }
}

.navbar-light .navbar-toggler {
  padding: 0;
  position: relative;
  border-radius: 0;
  border: 1px solid #e1e0de;
  width: 36px;
  height: 36px;
  border-radius: 3px;

  img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    &:last-child {
      display: none;
    }
  }
}

/* Start navbar toggler */
#nav-icon {
  width: 36px;
  height: 36px;
  position: relative;
  transform: rotate(0deg);
  transition: 0.5s ease-in-out;
  cursor: pointer;
  text-align: center;
}

#nav-icon span {
  display: block;
  position: absolute;
  height: 2px;
  width: 100%;
  background: #ef4273;
  border-radius: 10px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: 0.25s ease-in-out;
}

/* Icon 4 */

#nav-icon span:nth-child(1) {
  top: 12px;
  left: 14px;
  transform: rotate(-45deg);
  width: 8px;
  transform-origin: left top;
}

#nav-icon span:nth-child(2) {
  top: 20px;
  left: 11px;
  width: 16px;
  transform: rotate(-45deg);
  transform-origin: 10% center;
}

#nav-icon span:nth-child(3) {
  top: 25px;
  left: 13px;
  transform-origin: left top;
  transform: rotate(-45deg);
  width: 8px;
}

#nav-icon.open span:nth-child(1) {
  transform: rotate(45deg);
  top: 15px;
  left: 10px;
  width: 16px;
  transform-origin: 40% center;
}

#nav-icon.open span:nth-child(2) {
  width: 0%;
  opacity: 0;
}

#nav-icon.open span:nth-child(3) {
  transform: rotate(-45deg);
  transform-origin: -2% center;
  top: 22px;
  left: 12px;
  width: 16px;
}
/* End navbar toggler */

.navbar-light .navbar-nav .active > .nav-link,
.navbar-light .navbar-nav .nav-link.active,
.navbar-light .navbar-nav .nav-link.show,
.navbar-light .navbar-nav .show > .nav-link {
  color: $primaryColor;
}

.navbar-light .navbar-nav .nav-link {
  font-family: "Averta";
  font-weight: 500;
  color: #5b5863;
  font-size: 16px;
  line-height: 19px;
  transition: color 0.3s ease-in-out;
  padding: 12px 14px;

  position: relative;
  display: inline-block;
  z-index: 2;

  @media #{$maxTab} {
    line-height: 25px;
    padding-left: 0;
    font-size: 18px;
  }

  @media #{$betweentabdesk} {
    padding-left: 10px;
    padding-right: 10px;
  }
}

.navbar-light .navbar-nav .nav-link:focus,
.navbar-light .navbar-nav .nav-link:hover {
  color: $primaryColor !important;
}

.dropdown-toggle {
  &::after {
    border: 0;
    color: #928e9d;
    vertical-align: middle;
    margin-left: 5px;
    transition: all 0.3s ease-in-out;
    background-image: url(../imgs/arrow.svg);
    display: inline-block;
    width: 10px;
    height: 10px;
    background-position: center;
    background-repeat: no-repeat;
  }
}

/* End Header */


// footer
/* Start Footer */
footer {
  background-color: #f2265e;
  padding-top: 70px;

  @media #{$tablet} {
    padding-top: 45px;
  }
  @media #{$mobile} {
    padding-top: 15px;
  }
  &.homepage-footer {
    padding-top: 370px;
